const settings = {
  'default': {
    preventDefault: true,
    stopPlay: true,
    lockFocus: true,
    startFocus: true,
    focusBack: true,
    eventTimeout: 400,
    openCallback: false,
    closeCallback: false,
  },
};

const initModals = () => {
  const modalElements = document.querySelectorAll('.modal');
  modalElements.forEach((el) => {
    setTimeout(() => {
      el.classList.remove('modal--preload');
    }, 100);
  });
  modals = new Modals(settings);
  // ÐÑÐ¿Ð¾Ð»ÑÐ·ÑÐ¹ÑÐµ Ð² ÑÐ°Ð·ÑÐ°Ð±Ð¾ÑÐºÐµ ÑÐºÑÐ¿Ð¾ÑÑÐ¸ÑÑÐµÐ¼ÑÑ Ð¿ÐµÑÐµÐ¼ÐµÐ½Ð½ÑÑ modals, window ÑÐ´ÐµÐ»Ð°Ð½ Ð´Ð»Ñ Ð±ÑÐºÑÐ½Ð´Ð°
  window.modals = modals;
};

